
export class Admin_board_members_View {

	constructor () {
		this.clicked_btn = false;
		this.old_username = '';
		this.old_document = '';
		this.delete_btn_rendered = false;
		this.delete_btn_rendered2 = false;
	}

	articles_event_listener = (controllerFunction) => {

			const btns = Array.from(
				document.getElementsByClassName("admin_board_members_main_section_article_div_span")
			)

			let current_btn = null;

			    btns.forEach((btn, index) => {

			      btn.addEventListener("click", () => {

			      	current_btn = `admin_board_members_main_section_article_div_span${index + 1}`;

			        const btn_div = document.getElementById(
			          `admin_board_members_main_section_article_div${index + 1}`
			        );

			        if (this.clicked_btn) {
			          this.clicked_btn = false;
			          btn_div.style.display = "none";
			          btn.textContent = "Click Here";
			          btn.style.fontSize = "110%";
			          btn.style.color = "var(--fourth-color)";
			          btn.style.backgroundColor = "var(--third-color)";
			          btn.style.border = "var(--fourth-color) solid 2px";

			        } else {
			          this.clicked_btn = true;
			          btn_div.style.display = "flex";
			          btn.textContent = "X";
			          btn.style.fontSize = "200%";
			          btn.style.color = "darkred";
			          btn.style.border = "red solid 2px";
			          controllerFunction(index+1)
			        }

			        const other_divs = Array.from(
			        	document.getElementsByClassName(`admin_board_members_main_section_article_div1`)
			        )

			        other_divs.forEach((div,index2)=>{

			        	if(div.id !== btn_div.id ){

			        		div.style.display = 'none';

			        	}
			        })

		    		const reset_btns = Array.from(
						document.getElementsByClassName("admin_board_members_main_section_article_div_span")
					)

					reset_btns.forEach((btn2)=>{
						if(current_btn !== btn2.id){
				          btn2.textContent = "Click Here";
				          btn2.style.fontSize = "110%";
				          btn2.style.color = "var(--fourth-color)";
				          btn2.style.backgroundColor = "var(--third-color)";
				          btn2.style.border = "var(--fourth-color) solid 2px";
						}
					})
		    

			    });

			})
	}

	
	render_fetch_board_members = (state) => {

		const loading_div = 
		document.getElementById("admin_board_members_main_section_article_div1_loading");
		const no_board_members_msg = 
		document.getElementById("admin_board_members_main_section_article_div1_h3");
		const result = 
		document.getElementById("admin_board_members_main_section_article_div1_ul1")


		// loading elements
		if(state.loading){

			no_board_members_msg.style.display = 'none';
			result.style.display = 'none';
			loading_div.style.display = 'none';

			loading_div.style.display = 'block';

		}
		// finished loading, board_members users more than 1 
		else if (state.board_members  &&
			state.loading === false) {

			loading_div.style.display = 'none';
			no_board_members_msg.style.display = 'none';
			result.replaceChildren();

			result.style.display = 'flex';

			state.board_members.forEach((user)=>{

				const li = document.createElement("li");

				li.classList.add("admin_board_members_main_section_article_div1_ul1_li");

				li.textContent = user;

				result.appendChild(li)
			})
			
		}
		// finished loading, no board_members 
		else if(state.board_members === null) {
			loading_div.style.display = 'none';
			result.style.display = 'none';
			no_board_members_msg.style.display = 'none';

			no_board_members_msg.style.display = 'flex';
		}
	}

	render_add_board_members = (state) => {

		const loading_div = 
		document.getElementById("add_board_member_form_div_loading");
		const err = 
		document.getElementById("add_board_member_form_err_msg");
		const success = 
		document.getElementById("add_board_member_form_success_msg");

		// loading 
		if(state.loading){

			loading_div.style.display = 'none';
			success.style.display = 'none';
			err.style.display = 'none';

			loading_div.style.display = 'block';

		}
		// board memeber added successfully 
		else if (state.loading === false &&
			state.added_board_member === true) {

			success.style.display = 'none';
			loading_div.style.display = 'none';
			err.style.display = 'none';

			success.style.display = 'flex';
			success.textContent=`User Added Successfully ✅ Username: ${state.new_edited_user.username}, Password: ${state.new_edited_user.password}`
			
		}
		// board memeber not added successfully 
		else if (state.loading === false &&
			state.added_board_member === false &&
			state.attempted_add_board_member === true) {

				success.style.display = 'none';
				loading_div.style.display = 'none';
				err.style.display = 'none';

				err.style.display = 'flex';
		}
		else{
			loading_div.style.display = 'none';
			success.style.display = 'none';
			err.style.display = 'none';
		}

	}

	add_board_members =(modelFunction)=>{

		const submit_form = document.getElementById("add_board_member_form");

			submit_form.onsubmit =(event)=>{

				event.preventDefault();

				const username = 
				document.getElementById("add_board_member_form_username")

				const password = 
				document.getElementById("add_board_member_form_password")

				modelFunction(username.value,password.value)

				password.value = "";
				username.value = "";
			}

	}

	render_edit_board_members = (state) => {

	    const loading_div = document.getElementById("admin_board_members_main_section_article_div4_div1_loading");
	    const no_members = document.getElementById("admin_board_members_main_section_article_div4_div1_h4");
	    const members = document.getElementById("admin_board_members_main_section_article_div4_div1_h42");
	    const loading_div2 = document.getElementById("edit_board_member_form_div_loading");
	    const err = document.getElementById("edit_board_member_form_err_msg");
	    const success = document.getElementById("edit_board_member_form_success_msg");
	    const ul = document.getElementById("admin_board_members_main_section_article_div4_div1_ul");
	    const form = document.getElementById("edit_board_member_form");

	    // Helper to hide everything
	    const hideAll = () => {
	        loading_div.style.display = 'none';
	        no_members.style.display = 'none';
	        members.style.display = 'none';
	        success.style.display = 'none';
	        err.style.display = 'none';
	        loading_div2.style.display = 'none';
	        form.style.display = 'none';
	    }

	    // 1️⃣ Loading
	    if (state.loading) {
	        hideAll();
	        ul.replaceChildren()
	        loading_div.style.display = 'block';
	        loading_div2.style.display = 'block';
	    }
	    // 2️⃣ Success message after edit
	    else if (state.edited_board_member === true) {
	        hideAll();
	        ul.replaceChildren()
	        members.style.display = 'flex';
	        success.style.display = 'flex';
	        form.style.display = 'block';
	        success.textContent = `Board Member Username or Password Changed Successfully ✅ Username: ${state.new_edited_user.username}, Password: ${state.new_edited_user.password}`;
	        ul.replaceChildren();
	        state.board_members.forEach((bm) => {
	            const li = document.createElement("li");
	            li.classList.add("admin_board_members_main_section_article_div4_div1_ul_li");
	            li.setAttribute("name", bm);
	            li.textContent = bm;

	            li.addEventListener("click", (event) => {
	                this.old_username = event.target.getAttribute("name");

	                // Remove 'selected' class from all other LIs
	                const allLi = ul.querySelectorAll(".admin_board_members_main_section_article_div4_div1_ul_li");
	                allLi.forEach(otherLi => otherLi.classList.remove("admin_board_members_main_section_article_div4_div1_ul_li_selected"));

	                // Add 'selected' to clicked li
	                li.classList.add("admin_board_members_main_section_article_div4_div1_ul_li_selected");
	            });

	            ul.appendChild(li);
	        });

	    }
	    // 3️⃣ Error message if attempted edit failed
	    else if (state.edited_board_member === false && state.attempted_edit_board_member === true) {
	        hideAll();
	        ul.replaceChildren();
	        members.style.display = 'flex'; 
	        err.style.display = 'flex';
	        form.style.display = 'block';;
	        state.board_members.forEach((bm) => {
	            const li = document.createElement("li");
	            li.classList.add("admin_board_members_main_section_article_div4_div1_ul_li");
	            li.setAttribute("name", bm);
	            li.textContent = bm;

	            li.addEventListener("click", (event) => {
	                this.old_username = event.target.getAttribute("name");

	                // Remove 'selected' class from all other LIs
	                const allLi = ul.querySelectorAll(".admin_board_members_main_section_article_div4_div1_ul_li");
	                allLi.forEach(otherLi => otherLi.classList.remove("admin_board_members_main_section_article_div4_div1_ul_li_selected"));

	                // Add 'selected' to clicked li
	                li.classList.add("admin_board_members_main_section_article_div4_div1_ul_li_selected");
	            });

	            ul.appendChild(li);
	        });
	    }
	    // 4️⃣ Board members loaded
	    else if (state.board_members ) {
	        hideAll();
	        ul.replaceChildren()
	        members.style.display = 'flex';
	        form.style.display = 'block';
	        state.board_members.forEach((bm) => {
	            const li = document.createElement("li");
	            li.classList.add("admin_board_members_main_section_article_div4_div1_ul_li");
	            li.setAttribute("name", bm);
	            li.textContent = bm;

	            li.addEventListener("click", (event) => {
	                this.old_username = event.target.getAttribute("name");

	                // Remove 'selected' class from all other LIs
	                const allLi = ul.querySelectorAll(".admin_board_members_main_section_article_div4_div1_ul_li");
	                allLi.forEach(otherLi => otherLi.classList.remove("admin_board_members_main_section_article_div4_div1_ul_li_selected"));

	                // Add 'selected' to clicked li
	                li.classList.add("admin_board_members_main_section_article_div4_div1_ul_li_selected");
	            });

	            ul.appendChild(li);
	        });
	    }
	    // 5️⃣ No board members
	    else {
	        hideAll();
	        no_members.style.display = "flex";

	    }
	}

	edit_board_members = (modelFunction)=>{

		const submit_form = 
		document.getElementById('edit_board_member_form');

		const username = 
		document.getElementById("edit_board_member_form_username")

		const password = 
		document.getElementById("edit_board_member_form_password")

		submit_form.onsubmit = (event)=>{

			event.preventDefault();

			modelFunction(this.old_username,username.value,password.value)

			username.value = "";
			password.value = ""
		}
	}

	render_delete_board_members = (state) => {

	    const loading_div = document.getElementById("admin_board_members_main_section_article_div3_div1_loading");
	    const no_members = document.getElementById("admin_board_members_main_section_article_div3_div1_h4");
	    const members = document.getElementById("admin_board_members_main_section_article_div3_div1_h42");

	    const delete_msg = document.getElementById("admin_board_members_main_section_article_div3_div2_h5");
	    const delete_btn = document.getElementById("admin_board_members_main_section_article_div3_div2_btn");

	    const loading_div2 = document.getElementById("admin_board_members_main_section_article_div3_div3_loading");
	    const err = document.getElementById("admin_board_members_main_section_article_div3_div3_err");
	    const success = document.getElementById("admin_board_members_main_section_article_div3_div3_success");

	    const ul = document.getElementById("admin_board_members_main_section_article_div3_div1_ul");

	   	const hideAll = () => {
	        loading_div.style.display = 'none';
	        no_members.style.display = 'none';
	        members.style.display = 'none';
	        success.style.display = 'none';
	        err.style.display = 'none';
	        loading_div2.style.display = 'none';
	        delete_msg.style.display = 'none';
	        delete_btn.style.display = 'none';
	    }

		if(state.loading){
			hideAll();
			ul.replaceChildren()
			loading_div.style.display = 'block';
			loading_div2.style.display = 'block';
		}
		// success delete
		else if (state.loading === false &&
			state.attempted_delete_board_member === false &&
			state.deleted_board_member === true) {

			hideAll();
			ul.replaceChildren()

			if(state.board_members){
			members.style.display = 'flex';
			}
			else{
				no_members.style.display = 'flex';
			}

	        success.style.display = 'flex';
	       	delete_msg.style.display = 'flex';
	        delete_btn.style.display = 'flex';


	        success.textContent = `Board member: ${this.old_username} successfully deleted ✅`


	        state.board_members.forEach((bm) => {


	            const li = document.createElement("li");
	            li.classList.add("admin_board_members_main_section_article_div3_div1_ul_li");
	            li.setAttribute("name", bm);
	            li.textContent = bm;

	            li.addEventListener("click", (event) => {

	                this.old_username = event.target.getAttribute("name");
	

	                // Remove 'selected' class from all other LIs
	                const allLi = ul.querySelectorAll(".admin_board_members_main_section_article_div3_div1_ul_li");
	                allLi.forEach(otherLi => otherLi.classList.remove("admin_board_members_main_section_article_div3_div1_ul_li_selected"));

	                // Add 'selected' to clicked li
	                li.classList.add("admin_board_members_main_section_article_div4_div1_ul_li_selected");
	            });

	            ul.appendChild(li);
	        });
			
		}
		// unsuccessful delete
		else if (state.loading === false &&
			state.attempted_delete_board_member === true &&
			state.deleted_board_member === false){

			hideAll();
			ul.replaceChildren();

			members.style.display = 'flex';
	        err.style.display = 'flex';
	       	delete_msg.style.display = 'flex';
	        delete_btn.style.display = 'flex';

	        err.textContent = `Error deleting Board Member, Please try again ❌`
	        
	        state.board_members.forEach((bm) => {

	            const li = document.createElement("li");
	            li.classList.add("admin_board_members_main_section_article_div3_div1_ul_li");
	            li.setAttribute("name", bm);
	            li.textContent = bm;

	            li.addEventListener("click", (event) => {
	            	console.log(("click"))
	                this.old_username = event.target.getAttribute("name");

	                // Remove 'selected' class from all other LIs
	                const allLi = ul.querySelectorAll(".admin_board_members_main_section_article_div3_div1_ul_li");
	                allLi.forEach(otherLi => otherLi.classList.remove("admin_board_members_main_section_article_div3_div1_ul_li_selected"));

	                // Add 'selected' to clicked li
	                li.classList.add("admin_board_members_main_section_article_div4_div1_ul_li_selected");
	            });

	            ul.appendChild(li);
	        });
		}
		// load members successul
		else if (state.loading === false &&
			state.board_members){

			hideAll();
			ul.replaceChildren()

			members.style.display = 'flex';
	       	delete_msg.style.display = 'flex';
	        delete_btn.style.display = 'flex';

	        state.board_members.forEach((bm) => {

	            const li = document.createElement("li");
	            li.classList.add("admin_board_members_main_section_article_div3_div1_ul_li");
	            li.setAttribute("name", bm);
	            li.textContent = bm;

	            li.addEventListener("click", (event) => {
	            	console.log(("click"))
	                this.old_username = event.target.getAttribute("name");

	                // Remove 'selected' class from all other LIs
	                const allLi = ul.querySelectorAll(".admin_board_members_main_section_article_div3_div1_ul_li");
	                allLi.forEach(otherLi => otherLi.classList.remove("admin_board_members_main_section_article_div3_div1_ul_li_selected"));

	                // Add 'selected' to clicked li
	                li.classList.add("admin_board_members_main_section_article_div4_div1_ul_li_selected");
	            });

	            ul.appendChild(li);
	        });


		}
		// load members unsuccessful 
		else {

			hideAll();
			ul.replaceChildren()

			no_members.style.display = 'flex';

		}
	}

	delete_board_members = (modelFunction)=>{

		const delete_btn = 
		document.getElementById("admin_board_members_main_section_article_div3_div2_btn");

		if(!this.delete_btn_rendered){

			delete_btn.addEventListener("click", (event)=>{

				modelFunction(this.old_username)
			})

			this.delete_btn_rendered =  true;

		}

	}

	render_fetch_board_documents = (state) => {

		const loading_div = 
		document.getElementById("admin_board_members_main_section_article_div5_loading");
		const no_board_documents_msg = 
		document.getElementById("admin_board_members_main_section_article_div5_h4");
		const result = 
		document.getElementById("admin_board_members_main_section_article_div5_ul1")


		// loading elements
		if(state.loading){

			no_board_documents_msg.style.display = 'none';
			result.style.display = 'none';
			loading_div.style.display = 'none';

			loading_div.style.display = 'block';

		}
		// finished loading, board_documents users more than 1 
		else if (state.board_documents &&
			state.loading === false) {

			loading_div.style.display = 'none';
			no_board_documents_msg.style.display = 'none';
			result.replaceChildren();

			result.style.display = 'flex';

			state.board_documents.forEach((bd)=>{

				const li = document.createElement("li");
				const a = document.createElement("a");

				li.classList.add("admin_board_members_main_section_article_div5_ul1_li");
				a.classList.add("admin_board_members_main_section_article_div5_ul1_li_a");

				if(process.env.NODE_ENV === "production"){
					a.setAttribute("href", bd.production_url)
				}
				else{
					a.setAttribute("href", bd.development_url)
				}

				a.setAttribute("target", "_blank")

				a.textContent = bd.name

				li.appendChild(a)
				result.appendChild(li)
			})
			
		}
		// finished loading, no board_documents 
		else{
			loading_div.style.display = 'none';
			result.style.display = 'none';
			no_board_documents_msg.style.display = 'none';

			no_board_documents_msg.style.display = 'flex';
		}
	}


	render_add_board_documents = (state) => {

		const loading_div = 
		document.getElementById("add_board_document_form_div_loading");
		const err = 
		document.getElementById("add_board_document_form_err_msg");
		const success = 
		document.getElementById("add_board_document_form_success_msg");

		// loading 
		if(state.loading){

			loading_div.style.display = 'none';
			success.style.display = 'none';
			err.style.display = 'none';

			loading_div.style.display = 'block';

		}
		// board document added successfully 
		else if (state.loading === false &&
			state.added_board_document === true) {

			success.style.display = 'none';
			loading_div.style.display = 'none';
			err.style.display = 'none';

			success.style.display = 'flex';
			success.textContent=`Document Added Successfully ✅ : ${state.added_board_document_name}`
			
		}
		// board document not added successfully 
		else if (state.loading === false &&
			state.added_board_document=== false &&
			state.attempted_add_board_document === true) {

				success.style.display = 'none';
				loading_div.style.display = 'none';
				err.style.display = 'none';

				err.style.display = 'flex';
		}
		else if (state.board_documents) {
			loading_div.style.display = 'none';
			success.style.display = 'none';
			err.style.display = 'none';
		}
		else{
			loading_div.style.display = 'none';
			success.style.display = 'none';
			err.style.display = 'none';
		}

	}

	add_board_documents =(modelFunction)=>{

		const submit_form = document.getElementById("add_board_document_form");
		const fileInput = document.getElementById("add_board_document_form_upload_input");

			submit_form.onsubmit =(event)=>{

				event.preventDefault();

				const name = 
				document.getElementById("add_board_document_form_name")
				
				const file = fileInput.files[0];

				if(!file){

					file = null
				}
				modelFunction(name.value,file)

				submit_form.reset();
			}

	}

	render_delete_board_documents = (state) => {

	    const loading_div = document.getElementById("admin_board_members_main_section_article_div7_div1_loading");
	    const no_members = document.getElementById("admin_board_members_main_section_article_div7_div1_h4");
	    const members = document.getElementById("admin_board_members_main_section_article_div7_div1_h42");

	    const delete_msg = document.getElementById("admin_board_members_main_section_article_div7_div2_h5");
	    const delete_btn = document.getElementById("admin_board_members_main_section_article_div7_div2_btn");

	    const loading_div2 = document.getElementById("admin_board_members_main_section_article_div7_div3_loading");
	    const err = document.getElementById("admin_board_members_main_section_article_div7_div3_err");
	    const success = document.getElementById("admin_board_members_main_section_article_div7_div3_success");

	    const ul = document.getElementById("admin_board_members_main_section_article_div7_div1_ul");

	   	const hideAll = () => {
	        loading_div.style.display = 'none';
	        no_members.style.display = 'none';
	        members.style.display = 'none';
	        success.style.display = 'none';
	        err.style.display = 'none';
	        loading_div2.style.display = 'none';
	        delete_msg.style.display = 'none';
	        delete_btn.style.display = 'none';
	    }

		if(state.loading){
			hideAll();
			ul.replaceChildren()
			loading_div.style.display = 'block';
			loading_div2.style.display = 'block';
		}
		// success delete
		else if (state.loading === false &&
			state.attempted_delete_board_document === false &&
			state.deleted_board_document === true) {

			hideAll();
			ul.replaceChildren()

			if(state.board_documents){
			members.style.display = 'flex';
			}
			else{
				no_members.style.display = 'flex';
			}

	        success.style.display = 'flex';
	       	delete_msg.style.display = 'flex';
	        delete_btn.style.display = 'flex';


	        success.textContent = `Board Document: ${state.added_board_document_name} successfully deleted ✅`


	        state.board_documents.forEach((bm) => {


	            const li = document.createElement("li");
	            li.classList.add("admin_board_members_main_section_article_div7_div1_ul_li");
	            li.setAttribute("name", bm.name);
	            li.textContent = bm.name;

	            li.addEventListener("click", (event) => {
	            	console.log("click")
	                this.old_document = event.target.getAttribute("name");
	

	                // Remove 'selected' class from all other LIs
	                const allLi = ul.querySelectorAll(".admin_board_members_main_section_article_div7_div1_ul_li");
	                allLi.forEach(otherLi => otherLi.classList.remove("admin_board_members_main_section_article_div7_div1_ul_li_selected"));

	                // Add 'selected' to clicked li
	                li.classList.add("admin_board_members_main_section_article_div7_div1_ul_li_selected");
	            });

	            ul.appendChild(li);
	        });
			
		}
		// unsuccessful delete
		else if (state.loading === false &&
			state.attempted_delete_board_document === true &&
			state.deleted_board_document === false){

			hideAll();
			ul.replaceChildren();

			members.style.display = 'flex';
	        err.style.display = 'flex';
	       	delete_msg.style.display = 'flex';
	        delete_btn.style.display = 'flex';

	        err.textContent = `Error deleting Board Member, Please try again ❌`
	        
	        state.board_documents.forEach((bm) => {


	            const li = document.createElement("li");
	            li.classList.add("admin_board_members_main_section_article_div7_div1_ul_li");
	            li.setAttribute("name", bm.name);
	            li.textContent = bm.name;

	            li.addEventListener("click", (event) => {

	                this.old_document = event.target.getAttribute("name");
	

	                // Remove 'selected' class from all other LIs
	                const allLi = ul.querySelectorAll(".admin_board_members_main_section_article_div7_div1_ul_li");
	                allLi.forEach(otherLi => otherLi.classList.remove("admin_board_members_main_section_article_div7_div1_ul_li_selected"));

	                // Add 'selected' to clicked li
	                li.classList.add("admin_board_members_main_section_article_div7_div1_ul_li_selected");
	            });

	            ul.appendChild(li);
	        });
		}
		// load members successul
		else if (state.loading === false &&
			state.board_documents){

			hideAll();
			ul.replaceChildren()

			members.style.display = 'flex';
	       	delete_msg.style.display = 'flex';
	        delete_btn.style.display = 'flex';

	        state.board_documents.forEach((bm) => {


	            const li = document.createElement("li");
	            li.classList.add("admin_board_members_main_section_article_div7_div1_ul_li");
	            li.setAttribute("name", bm.name);
	            li.textContent = bm.name;

	            li.addEventListener("click", (event) => {

	                this.old_document = event.target.getAttribute("name");
	

	                // Remove 'selected' class from all other LIs
	                const allLi = ul.querySelectorAll(".admin_board_members_main_section_article_div7_div1_ul_li");
	                allLi.forEach(otherLi => otherLi.classList.remove("admin_board_members_main_section_article_div7_div1_ul_li_selected"));

	                // Add 'selected' to clicked li
	                li.classList.add("admin_board_members_main_section_article_div7_div1_ul_li_selected");
	            });

	            ul.appendChild(li);
	        });

		}
		else{
			hideAll();
			ul.replaceChildren()

			no_members.style.display = 'flex';
		}
	}

	delete_board_documents = (modelFunction)=>{

		const delete_btn = 
		document.getElementById("admin_board_members_main_section_article_div7_div2_btn");

		if(!this.delete_btn_rendered2){

			delete_btn.addEventListener("click", (event)=>{

				modelFunction(this.old_document)
			})

			this.delete_btn_rendered2 =  true;

		}

	}
}